@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  border: 2px solid black;
  font-family: "Poppins";
  padding: 10px 20px;
  font-size: 18px;
}

button:hover {
  cursor: pointer;
  color: white;
  background-color: black;
  transition: all 0.2s;
}

.movieContainer {
  margin: 80px auto;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(300px, 1fr))[1];
      grid-template-columns: repeat(1, minmax(300px, 1fr));
  -ms-grid-rows: 1;
      grid-template-rows: 1;
  margin-bottom: 100px;
}

.movieContent {
  text-align: center;
  box-shadow: 3px 3px 15px grey;
  padding: 0px 30px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.movieContent img {
  width: 60%;
}

.movieContent p {
  font-size: 14px;
  width: 500px;
}

.creditsContainer {
  text-align: center;
}

.creditsContainer li {
  font-size: 16px;
  list-style: none;
  color: darkblue;
}

.creditsContainer span {
  text-decoration: none;
  color: black;
  margin: 0px 10px;
}

@media screen and (max-width: 825px) {
  .movieContainer {
    overflow: hidden;
  }
  .movieContent {
    text-align: center;
    box-shadow: 3px 3px 15px grey;
    padding: 0px 10px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    width: 95%;
    text-align: center;
  }
  .movieContent p {
    width: 100%;
  }
}
/*# sourceMappingURL=MoviesInfo.css.map */
body {
  font-size: 18px;
  font-family: "Poppins";
  font-weight: 400, 600;
}

.nowPlaying {
  margin: 150px 0px;
}

.nowContainer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(350px, 1fr))[auto-fit];
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  -ms-grid-rows: auto;
      grid-template-rows: auto;
  grid-gap: 50px;
  gap: 50px;
  margin: auto 170px;
}

.nowContainer a {
  text-decoration: none;
  color: black;
}

.nowCard {
  text-align: center;
  box-shadow: 3px 3px 15px grey;
  height: 100%;
}

.nowCard h2 {
  font-size: 22px;
  margin: 0;
}

.nowCard p {
  font-size: 14px;
  line-height: 25px;
  letter-spacing: 0.3px;
  margin: 0px 20px;
  margin-bottom: 20px;
}

.nowCard:hover {
  box-shadow: 3px 3px 10px grey;
  cursor: pointer;
  transition: box-shadow 0.5s;
}

img {
  width: 100%;
}

@media screen and (max-width: 690px) {
  .nowContainer {
    margin: auto auto;
  }
}
/*# sourceMappingURL=NowPlaying.css.map */
.searchContainer {
  background-color: white;
  position: absolute;
  overflow: hidden;
  width: 100%;
  margin: auto auto;
  left: 0;
  right: 0;
  top: 0;
}

form input {
  width: 90%;
  font-family: "Poppins";
  padding: 10px 0px 10px 20px;
  border: none;
  border-bottom: 3px solid black;
}

.searchDropDown {
  border-bottom: 2px solid grey;
  border-left: 2px solid grey;
  border-right: 2px solid grey;
  padding-left: 20px;
}

.searchDropDown a {
  text-decoration: none;
  color: black;
}

.searchDropDown h3,
.searchDropDown p {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.searchDropDown h3 {
  font-size: 16px;
  margin: 0;
}

.searchDropDown p {
  margin: 0;
  font-size: 14px;
}

.searchDropDown img {
  width: 30%;
}
/*# sourceMappingURL=SearchBox.css.map */
